/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
}
@media (max-width: 768px) {
  html {
    font-size: 50%;
  }
}
@media (max-width: 480px) {
  html {
    font-size: 46%;
  }
}

body {
  min-height: 100%;
  background-color: #131313;
  color: #FFFFFF;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}

body {
  font-family: "Abel", sans-serif;
  font-size: 1.6rem;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

.container-sm {
  width: 85%;
  max-width: 1100px;
  margin: 0 auto;
}

.section {
  margin: 5rem auto;
  margin-top: 12rem;
}
@media (max-width: 768px) {
  .section {
    margin-top: 5rem;
  }
}

a {
  text-decoration: none;
}

ul, li {
  list-style: none;
}

img {
  max-width: 100%;
  width: inherit;
  height: inherit;
}

h1, h2, h3 {
  font-family: "Abel", sans-serif;
  margin: 0;
  font-weight: 200;
  color: #f3f3f3;
  text-align: center;
}

h1 {
  font-size: 3rem;
}

h2 {
  font-size: 2.6rem;
}

h3 {
  font-size: 2rem;
}

p {
  color: #bcbcbc;
  font-size: 2.6rem;
}

.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.9), rgba(0, 0, 0, 0.9)), url("/build/img/hero.webp");
  background-position: center center;
  background-size: cover;
  background-repeat: no-repeat;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hero .logo {
  font-size: 6rem;
}

.navbar-container {
  background-color: #000000;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: sticky;
  padding: 2rem;
  height: 10vh;
}
.navbar .logo {
  font-size: 3rem;
}
.navbar .nav {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}
.navbar .nav a {
  padding: 1rem 2rem 0.7rem 2rem;
  color: #FFFFFF;
  font-size: 2rem;
  border-bottom: 0.3rem solid transparent;
  transition: color, border 0.3s ease;
}
.navbar .nav a:hover {
  color: #00c9ac;
  border-bottom: 0.3rem solid #FFFFFF;
}

.sticky {
  position: fixed;
  top: 0;
  width: 100%;
}

.logo {
  color: #00c9ac;
  font-weight: 700;
}
.logo span {
  color: #FFFFFF;
}